import { Howl } from "howler";
import { Entity, Scene } from "nopun-ecs";

import { Sound } from "../../component";

import DebrisSound from "./Debris.wav";

export function addDebrisToScene(scene: Scene): Entity {
    return scene.entities.create()
        .add(Sound, {
            value: new Howl({
                src: [DebrisSound],
                volume: .1
            })
        });
}