import { Howl } from "howler";
import { Entity, Scene } from "nopun-ecs";

import { Sound } from "../../component";

import Music from "./Music.mp3";

export function addMusicToScene(scene: Scene): Entity {
    return scene.entities.create()
        .add(Sound, {
            value: new Howl({
                src: [Music],
                volume: .1,
                loop: true
            })
        });
}